import {Component, OnInit} from '@angular/core';
import {ActionSheetController, Platform, PopoverController, ToastController} from '@ionic/angular';
import {CreateChatComponent} from './create-chat/create-chat.component';
import {LoaderService} from 'src/services/loader.service';
import {ChatService} from '../../services/chat.service';
import {Storage} from '@ionic/storage';
import {Router} from '@angular/router';
import {AnalyticsService} from "../../services/analytics.service";
import * as moment from 'moment';
import {Device} from "@ionic-native/device/ngx";
import {ToastService} from "../../services/toast.service";
import {EventProviderService} from "../../services/event-provider.service";
import { TranslateService } from '@ngx-translate/core';
import {Location} from '@angular/common';
import { CommonService } from 'src/services/common.service';
import { ApiService } from 'src/services/api.service';

@Component({
    selector: 'app-chat',
    templateUrl: './chat.page.html',
    styleUrls: ['./chat.page.scss'],
})
export class ChatPage implements OnInit {
    theme: any;
    conversations: any = [];
    user: any;
    ch_id: any;
    start_time_analytics: any;
    page_name: any;
    custom_name: any;
    device_id: any;
    private_chatrooms: any = [];
    is_show_no_record: boolean = false;
    alertMessage: any;
     pressTimer: any;

    constructor(
        private toastCtrl: ToastController,
        public popoverController: PopoverController,
        private loader: LoaderService,
        private chatService: ChatService,
        private storage: Storage,
        public router: Router,
        private analytics: AnalyticsService,
        private platform: Platform,
        private device: Device,
        private toast: ToastService,
        private event_provider: EventProviderService,
        private translate: TranslateService,
        private location: Location,
        private common:CommonService,
        public actionSheetController: ActionSheetController,
        public apiService: ApiService

    ) {
        // preparing device data
        // if(this.platform.is('android') || this.platform.is('iphone')) {
        //     let dev_id = this.device.uuid;
        //     if (dev_id){
        //         // this.device_id =  dev_id;
        //         this.device_id =  'church';
        //     }
        // }
        // get translated alert message
        let interval = setInterval(() => {
            this.translate.get('ALERTS').subscribe( (res: any) => {
                this.alertMessage = res;
                clearInterval(interval);
            });
        }, 2000);
        
        this.location.subscribe(res=>{
            this.doRefresh();
        })
    }

    ngOnInit() {
    

        // let dev_id = this.device.uuid;
        // if (dev_id){
        //     this.device_id = dev_id;
        //     this.getTheme();
        // } else {
        //     this.getTheme();
        // }

        // this.event_provider.userLogout.subscribe(value => {              // listening event
        //     this.ngOnInit()
        // });
    }

    gotobackPage() {
        this.location.back();
       
    }

    getUser() {
        // getting user from local storage
        this.storage.get('user').then((val: any) => {
            if (val != null && val != 'guest') {
                this.user = val;
            }
        });
    }

    async getTheme() {
        var  url:any = new URL(window.location.href);
        console.log(url.pathname)
        // getting theme from local storage
        await this.event_provider.getChurchData.subscribe(value => {
            if (!(Object.keys(value).length === 0)) {
                this.theme = value.church;
                this.ch_id = this.theme.ch_id;
                this.getUser();
                let timer = setTimeout(() => {
                    if (this.user) {
                        this.getChats(this.theme.ch_id);
                    } else {
                        this.router.navigate(['/login', {
                            page: "chat",
                            path: url.pathname,
                            
                          }]);
                        this.getChats(this.theme.ch_id);
                        this.toast.presentToastWithOptions((this.alertMessage ? this.alertMessage.login_to_message : 'Login to Message'), (this.alertMessage ? this.alertMessage.LoginRequired : 'Login Required'), "warning");
                        // return;
                    }
                }, 1000);
                // this.getChats(this.theme.ch_id);
            }
        });
    }

    getChats(id) {
        // getting chat rooms
        var uid = this.user ? this.user.user.id : "guest"
        this.loader.presentLoading().then( () => { });
        setTimeout(() => {
            this.loader.stopLoading();
        }, 1000);
        this.chatService.getChats(id, uid).subscribe(async (res: any) => {
            if (res.status) {
                this.conversations = res.conversation[0].filter(x => x.private_chatroom !== 'true');
                this.private_chatrooms = res.conversation[0].filter(x => x.private_chatroom === 'true');
                
                if (this.private_chatrooms.length > 0) {
                if (this.device_id && this.user) {
                    let my_private_rooms = this.private_chatrooms.filter(x => {
                        let check_device = x.devices.find(y => y.device_id === this.device_id);
                        let check_member = x.members.find(y => y.account_donor_id === this.user.user.id);
                        if (check_device || check_member) {
                            return x;
                        }
                    });
                    if (my_private_rooms.length > 0) {
                        this.conversations = this.conversations.concat(my_private_rooms);
                    }
                } else if (this.device_id) {

                    let my_private_rooms = this.private_chatrooms.filter(x => {
                        let check = x.devices.find(y => y.device_id === this.device_id);
                        if (check) {
                            return x;
                        }
                    });
                    if (my_private_rooms.length > 0) {
                        this.conversations = this.conversations.concat(my_private_rooms);
                    }
                } else if (this.user) {

                    let my_private_rooms = this.private_chatrooms.filter(x => {
                        let check = x.members.find(y => y.account_donor_id === this.user.user.id);
                        if (check) {
                            return x;
                        }
                    });
                    if (my_private_rooms.length > 0) {

                       this.conversations = this.conversations.concat(my_private_rooms);
                    }
                }
            }
            }
            this.is_show_no_record = true;
            this.conversations = this.conversations.sort((a, b) => new Date(a.last_message_date) >= new Date(b.last_message_date) ? -1 : 1);
            this.private_chatrooms = this.private_chatrooms.sort((a, b) => new Date(a.last_message_date) >= new Date(b.last_message_date) ? -1 : 1);
            this.loader.stopLoading();
        }, (err: any) => {
            this.loader.stopLoading();
        });
        // })
    }

    // loadChatBox(conversation) {
    //     let data = JSON.stringify(conversation);
    //     this.router.navigate(['/chat-detail',
    //         {
    //             conversation: data,
    //             ch_id: this.theme.ch_id
    //         }
    //     ]);
    // }

    loadChatBox(conversation) {
        let data = JSON.stringify(conversation);
        this.router.navigate(['/chat-detail',
            {
                conversation: data,
                ch_id: this.theme.ch_id
            }
        ]);
    }

    async enter_password() {
        if (this.user) {
        const popover = await this.popoverController.create({
            component: CreateChatComponent,
            translucent: true,
            cssClass: 'privateChatPopover'
        });
            popover.onDidDismiss().then(res => {
                if (res.data) {
                    if (res.data.pass) {
                        this.enter_private_chatroom(res.data.pass);
                    }
                }
            });
        return await popover.present();
    } else {
        this.toast.presentToastWithOptions((this.alertMessage ? this.alertMessage.LogintoEnterPrivateChatroom : 'Login to Enter Private Chatroom'), (this.alertMessage ? this.alertMessage.WarningMessage : 'Login!'), 'warning');
        }
    }

    enter_private_chatroom(pass){
        let private_room = this.private_chatrooms.find( x => x.password === pass);
        if (private_room){
            let check = this.conversations.find( x => (x.password === private_room.password) && (x.name === private_room.name));
            if (check){
                this.toast.presentToastWithOptions((this.alertMessage ? this.alertMessage.YouAreAlreadyMemberOfThisChatRoom : 'You are already member of this chat room'), (this.alertMessage ? this.alertMessage.Alreadyexists : 'Already exists!'), 'warning');
            } else {
                this.conversations.push(private_room);
                this.loadChatBox(private_room);
                this.savingGroupMember(private_room.id);
                this.toast.presentToastWithOptions((this.alertMessage ? this.alertMessage.Successfullyjoinedchatroom : 'Successfully joined chatroom'), (this.alertMessage ? this.alertMessage.Success : 'Success'), 'success');
            }
        } else {
            this.toast.presentToastWithOptions((this.alertMessage ? this.alertMessage.Chatroomnotfound : 'Chatroom not found'), (this.alertMessage ? this.alertMessage.NotFound + '!' : 'Not Found!'), 'warning');
        }
    }

    savingGroupMember(chatroom_id){
        let body;
        // preparing data for saving group member
        if (this.device_id){
            body = JSON.stringify({
                auth: 'Bearer ' + this.user.auth.access_token,
                chatrooms_id: chatroom_id,
                account_donor_id: this.user.user.id,
                device_id: this.device_id,
            });
        } else {
            body = JSON.stringify({
                auth: 'Bearer ' + this.user.auth.access_token,
                chatrooms_id: chatroom_id,
                account_donor_id: this.user.user.id
            });
        }
        this.chatService.savegroupmember(body).subscribe( (res: any) => {
            if (res){
                //
            }

        });
    }

    setAnalytics() {
        let dev_id = '';
        let device = 'web';
        // preparing device data
        if(this.platform.is('android') || this.platform.is('iphone')) {
            dev_id = this.device.uuid;
            if (dev_id){
                device =  this.platform.platforms()[0];
            }
        }

        this.page_name = 'Chat';
        this.custom_name = 'Chat';

        // preparing duration data
        let end_time_analytics = moment();
        var duration:any = moment.duration(end_time_analytics.diff(this.start_time_analytics)).asSeconds();

        // preparing user data
        let userId = this.user ? this.user.user.id : 'guest';

        // preparing data for analytics
        const body = JSON.stringify({
            church_id: this.ch_id,
            latitude: this.theme.latitude ? this.theme.latitude : "",
            longitude: this.theme.longitude ? this.theme.longitude : "",
            timespent: duration,
            module: this.page_name,
            custom_name: this.custom_name,
            ip_address: "",
            device: device,
            device_id: dev_id,
            client_type: userId,
            event_type: ""
        });
        // calling function from service to update Analytics Data
        this.analytics.setAnalytics(body).subscribe((res: any) => {

        })
    }
    ionViewWillEnter() {
        // this.getTheme();
        this.ngOnInit();
        this.event_provider.getChurchData.subscribe(async (res: any) => {
            if (!(Object.keys(res).length === 0)) {
                // asking for password
                let app_pages = res.apppages.find(x => x.name === 'Chat');
                if (app_pages.password_protection_toggle === 'true') {
                    this.common.openPagePasswordModal(app_pages.page_protected_password, app_pages.selected_private_group);
                }
            }
        });
      
      }
    ionViewDidLeave(){
        this.setAnalytics();
    }
    ionViewDidEnter(){
       
        this.event_provider.userLogin.subscribe(res => {
            // setting start time for analytics
            console.log(res);
            this.start_time_analytics = moment();
            this.is_show_no_record = false;
            let dev_id = this.device.uuid;
            if (dev_id){
                this.device_id = dev_id;
                this.getTheme();
            } else {
                this.getTheme();
            }

            this.event_provider.userLogout.subscribe(value => {              // listening event
                this.ngOnInit()
            });
        });
    }

    onTouchStart(event, c, index) {
        this.pressTimer = setTimeout(() => {
            if(this.ch_id == 1265 || this.ch_id =='1265'){
            this.presentActionSheet(c, index);
            }
        }, 1000);
    }

    onTouchEnd(evet) {
        clearTimeout(this.pressTimer);
    }

    mutechat(c, index) {
        const body = JSON.stringify({
            chatroom_id: c.id,
            account_donor_id: this.user.user.id,
            mute: '1',
            church_id: this.ch_id,
        });

        this.apiService.getmute(body).subscribe((res: any) => {
            if (res) {
                if (c.muted == "0") {
                    this.conversations[index].muted = "1"
                } else {
                    this.conversations[index].muted = "0"
                }
            }
        });
    }

    markasRead(c, index) {
        const body = JSON.stringify({
            chatroom_id: c.id,
            account_donor_id: this.user.user.id,
        });
        this.apiService.readMsg(body).subscribe((res: any) => {
            if (res) {
                if (c.message_count > "0") {
                    this.conversations[index].message_count = "0"
                }
            }
        })
    }

    async presentActionSheet(c, index) {
        var str: string;
        if (c.muted == '0') {
            str = "Mute Chat"
        }
        else {
            str = "Unmute Chat"
        }
        const actionSheet = await this.actionSheetController.create({
            mode: 'ios',
            cssClass: 'cActionSheet',
            buttons: [{
                text: 'Mark As Read',
                handler: () => {
                    this.markasRead(c, index)
                    
                }
            }, {
                text: str,
                handler: () => {
                    this.mutechat(c, index);
                }
            }, {
                text: 'Cancel',
                role: 'cancel',
                handler: () => {
                  
                }
            }]
        });
        await actionSheet.present();
    }


   
doRefresh(event?) {
        this.getTheme()
        if(event){
            setTimeout(() => {
                event.target.complete();
              }, 2000);
        }
      }
}
